@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  background-color: #282c34;
}

.the-page {
  height: 100%;
  /*min-height: 100vh;*/

  margin: 0em;
  padding: 0 0 0 0;
  color: white;
}

.scrolling-wrapper {
  height: 70vh;
  overflow-x: auto;
  overflow-y: auto;
  white-space: nowrap;
}
.scroll-item {
  vertical-align: top;
  display: inline-block;
  height: 100%;
  padding: 1em;
  outline: 2px solid #282c34;
  background-color: #414855;
  border-radius: 10px;
}

.add-list-button {
  background-image: linear-gradient(
    32deg,
    rgb(33, 45, 39) 0%,
    rgb(18, 105, 116) 20%,
    rgb(18, 105, 116) 75%,
    rgb(33, 45, 39) 100%
  );
  margin: 1em 0em 1em 1em;
  display: inline-block;
  height: 50%;
  border-radius: 20px 20px 20px 20px;
  border: none;
  min-width: 5em;
}

.pro-con-item {
  display: block;
  margin: 5px 0;
  padding: 10px;
  width: 100%;
  border-radius: 10px;

  list-style-type: none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
}

.pro {
  background: #005e00;
}

.con {
  background: #950000;
}

.score {
  float: right;
  text-align: right;
}
.remove-button {
  float: right;
}

.text-input {
  width: 100%;
}

.score-input {
  width: 100%;
  text-align: right;
}

.grid-container {
  display: grid;
  grid-template-columns: 8fr 1fr 1fr;
}

.list-container {
  min-height: 100%;
  width: 400px;
  margin: 0em;
  padding: 0em;
  color: white;
}
.remove-list-button {
  float: right;
}
.add-list-item-button {
  background-image: linear-gradient(
    45deg,
    rgb(33, 45, 39) 0%,
    rgb(18, 105, 116) 50%
  );

  /*background-color: #244670;*/
  display: block;
  margin: 10px 0;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  border: #282c34 1px solid;
}

.list {
  margin-top: 1em;
  margin-bottom: 2em;
}

.title {
  text-align: center;
}

